@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Karla:ital,wght@0,200..800;1,200..800&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Karla:ital,wght@0,200..800;1,200..800&family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  box-sizing: border-box;
}

.app {
  display: block;
  scroll-snap-type: y mandatory;
  overflow-x: hidden;
  /* Disable sideways scroll */
  overflow-y: scroll;
  height: 100dvh;
  width: 100vw;
  background-color: #ffffff;
}

.fade-in {
  opacity: 1;
  /* No transition */
}

.fade-in.visible {
  opacity: 1;
}

.sticky-container {
  height: 100dvh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  scroll-snap-align: start;
  width: 100%;
  flex-direction: column;
  padding-left: 5%;
  padding-right: 5%;
  box-sizing: border-box;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
  color: #000;
  text-align: left;
  max-width: 90vw;
  white-space: pre-line;
}

h1,
h2 {
  font-family: "Merriweather", serif;
}

h3,
h4 {
  font-family: "Open Sans", sans-serif;
}

h1 {
  font-size: 6rem;
  font-weight: 900;
}

h2 {
  font-size: 4rem;
  font-weight: 600;
}

h3 {
  font-size: 2rem;
  font-weight: 500;
}

h4 {
  font-size: .85rem;
  font-weight: 600;
}

ol,
ul {
  width: 100%;
  max-width: 90vw;
  font-size: 1.5rem;
  color: #000;
  list-style-type: decimal;
  list-style-position: inside;
  text-indent: -1em;
  padding-left: 1em;
}

@media (max-width: 768px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 3rem;
  }

  h3 {
    font-size: 2rem;
  }

  ol,
  ul {
    font-size: 1.2rem;
  }

  ol li,
  ul li {
    margin-bottom: 1rem;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2rem;
  }

  h3 {
    font-size: 1rem;
  }

  ol,
  ul {
    font-size: 1rem;
  }

  ol li,
  ul li {
    margin-bottom: 0rem;
  }
}